<template>
	<section class="page-wrap">
		<app-header/>
		<header class="section-masthead">
			<figure>
				<lazy-image :src="imgSrc" :alt="imgAlt" />
			</figure>
			<h1>Philadelphia</h1>
		</header>
		<article>
			<p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Amet architecto cupiditate deleniti dignissimos doloribus exercitationem fugiat, in inventore ipsam molestiae natus, nihil nostrum perspiciatis quae quis rem sapiente unde vitae?</p>
			<p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Amet architecto cupiditate deleniti dignissimos doloribus exercitationem fugiat, in inventore ipsam molestiae natus, nihil nostrum perspiciatis quae quis rem sapiente unde vitae?</p>
			<video-wrapper src="https://player.vimeo.com/video/81020357?title=0&byline=0"
			               title="Philadelphia Lactose Free — Launch"/>
		</article>
		<app-footer />
	</section>
</template>

<script>
	
	import LazyImage from './LazyImage';
	import AppHeader from './AppHeader';
	import AppFooter from './AppFooter';
	import VideoWrapper from './VideoWrapper';
	
	export default {
		name: 'Philadelphia',
		components: {
			LazyImage,
			AppHeader,
			AppFooter,
			VideoWrapper,
		},
		data() {
			return {
				imgSrc: '/static/philadelphia.jpg',
				imgAlt: 'Philadelphia'
			}
		}
	}
</script>

<style scoped>
	.section-masthead {
		height: 100vh;
		display: flex;
		align-items: center;
		margin-top: 2rem;
	}
	
	figure {
		height: 100%;
		position: relative;
	}
	
	figure::after {
		content: '';
		position: absolute;
		top: -7.5px;
		left: 7.5px;
		width: 100%;
		height: 101%;
		background-color: var(--color__complementary);
		opacity: 0.5;
		mix-blend-mode: multiply;
		transform: rotate(0.5deg);
	}
	
	img {
		height: 100%;
		display: block;
		width: 100%;
		object-fit: cover;
	}
	
	h1 {
		padding: 1em 2em;
		background-color: var(--color__dark-alpha);
		transform: translateX(-10%);
		position: relative;
		z-index: 2;
		font-size: 5vw;
	}
	
	article {
		margin-top: 2em;
		margin-bottom: 2em;
	}
	
	@media screen and ( max-width: 64em ) {
		.section-masthead {
			margin-right: -3vw;
			margin-left: -3vw;
		}
		
		h1 {
			margin-left: -30%;
		}
	}
	
	@media screen and ( min-width: 64em ) {
		h1 {
			font-size: 3vw;
		}
	}
</style>



